
// 
import JSZip from 'jszip';
import FileSaver from 'file-saver';
// 
import axios from 'axios'
//  fileArr  exportName  fileUrl  fileName 
export function downloadZip(fileArr,exportName,fileUrl,fileName) {
    const zip = new JSZip();
    const promises = [];  // promise
    const arr = fileArr.filter(item => item[fileUrl]!==''); // 
    arr.forEach( item => {
        const promise = new Promise((resolve, reject) => {
            // 
            axios ({
                url: item[fileUrl],
                method: 'GET',
                responseType: 'blob'
            }).then((res) => {          // request
                resolve(res)
            }).catch((error) => {
                reject(error)
            })
        }).then((res) => {
            // 
            const name = item[fileName];
            //   zip.file(/,  ,{});
            zip.file(name, res.data ,{binary: true});
        })
        // promisepromise.all
        promises.push(promise);
    })
    Promise.all(promises).then(() => {
        // promiseres
        zip.generateAsync({
            type: "blob",
            compression: "DEFLATE",  // STORE DEFLATE
            compressionOptions: {
                level: 9               // 1~9    19
            }
        }).then((res) => {
            FileSaver.saveAs(res, exportName) // file-saver
        })
    })
}
